.Toolbar{
  overflow: auto;
  margin-left:auto;
  margin-right:0px;
}

.Toolbar input[type='image']{
  padding:15px 20px 15px 20px;
  margin-left:5px;
}

@media only screen and (max-width: 900px) {

  .Toolbar{
    overflow: auto;
    position:absolute;
    bottom:5px;
    right:5px;
    z-index:3;
  }

  .Toolbar input[type='image']{
    background-color:rgba(255, 255, 255, 0.8);
    padding:8px 8px;
    z-index:3;
  }
  
  
}

