.ScaleBar {
  position: fixed;
  z-index:2;
  left:5px;
  bottom:5px;
  width:100px;
  border-radius: 5px;
  padding: 5px;
  background-color:rgba(255, 255, 255, 0.8);
  
}

.ScaleBarContent {
  text-align: center;
  color:#00c7c0;
  border-left:1px dashed #00c7c0;
  border-right:1px dashed #00c7c0;
  border-bottom:1px dashed #00c7c0;
}