.MouseWatcher {
  position: fixed;
  z-index:2;
  left:115px;
  bottom:5px;
  width:150px;
  border-radius: 5px;
  padding: 5px;
  background-color:rgba(255, 255, 255, 0.8);
  display:none;
}

.MouseWatcherContent {
  text-align: center;
  color:#00c7c0;
  border-left:1px dashed #00c7c0;
  border-right:1px dashed #00c7c0;
  border-bottom:1px dashed #00c7c0;
}

@media (pointer: fine) {
  .MouseWatcher {
    display:inherit;
  }
}