.LayerSwitcher{
  height:calc(100% - 80px);
  top:80px;
  z-index:4;
  position: fixed;
  border-left:1px solid #00c7c0;
  right:0px;
  color:#00c7c0;
  visibility:hidden;
  transition:visibility 0.3s linear,opacity 0.3s linear;
  background-color:rgba(255, 255, 255, 0.8);
  overflow: auto;
}

/*
.LayerTree{
  overflow: auto;
  height:calc(60vh - 200px);
}
*/
.LayerLinkWrapper{
  display: flex;
  justify-content: right;
  align-items: baseline;
  padding-bottom: 15px;
}

.HeavyLinkWrapper{
  display: flex;
  justify-content: left;
  align-items: baseline;
  padding: 0;
}

.LayerSwitcher .logo{
  padding:0px;
  
}

.LayerSwitcher .logo img{
  width:450px; 
  border:none;
  background-color: transparent;
}

.SwitcherTitle {
  font-size:35px;
  font-weight: bolder;  
} 

.SwitcherToggler img{
  cursor:pointer;
}

.LayerSwitcher .explorer .folder .title{
  font-size:25px;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
  overflow: hidden !important;
  cursor:pointer;
}

.LayerSwitcher .explorer .folder .item{
  margin-left:1px;
}

.LayerSwitcher .explorer .folder .layerTitle{
  font-size:20px;
  text-overflow: ellipsis !important;
  overflow: hidden !important;
}

.HeavyLink, .LayerLink{
  font-size:22px;
}

.LayerSwitcher .explorer .folder .layerCheck{
  width:18px;
  height:18px;
  margin-top:6px;
}

.LayerSwitcher .explorer{
  color:#00c7c0;
}

.LayerSwitcher .explorer .folder{
  color:#00c7c0;
}

.LayerSwitcher .explorer .folder .title div{
  width: 40px;
  display:inline-block;
}


.LayerSwitcher .explorer .folder .content .item input{
  cursor:pointer;
}

.LayerSwitcher .explorer .folder .content{
  border-left:1px solid #00c7c0;
  padding-left: 15px;
  margin-left: 15px;
  display:none;
  visibility: hidden;
}

.LayerSwitcher .explorer .folder .content .item label{
  color:#00c7c0;
  margin-left:5px;
}

